import React, { Component } from 'react';
import { Route, Redirect, Switch } from "react-router-dom";
import Links from "./links";
import EditLinks from "./links/edit";
import Channel from "./channel";


class NovelRouter extends Component {
  render() {
    return (
    	<div>
        <Route path='/admin/novel'>
          <Switch>
            <Route path="/admin/novel/novelLinks/index" component={Links} />
            <Route path="/admin/novel/novelLinks/create" component={EditLinks} />
            <Route path="/admin/novel/novelLinks/update" component={EditLinks} />
            <Route path="/admin/novel/channel/index" component={Channel} />
            <Redirect from="/admin/novel" exact to='/admin/novel/links' />
            <Redirect from="/admin/novel/novelLinks" exact to='/admin/novel/novelLinks/index' />
            <Redirect from="/admin/novel/channel" exact to='/admin/novel/channel/index' />
          </Switch>
        </Route>
    	</div>
    )
  }
}

export default NovelRouter;